body {
  width: 100%;
  margin: 0;
  font-size: 16px;
  line-height: 1.5;
}

h1, h2, h3, p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  object-fit: cover;
  display: block;
}

.list {
  list-style-type: none;
}

.link {
  color: currentColor;
  text-decoration: none;
}

.search-form {
  width: 100%;
  text-align: center;
  background-color: #7878d2;
  outline: none;
  padding: 20px 0;
  position: fixed;
  top: 0;
  box-shadow: 0 1px 6px rgba(46, 47, 66, .08), 0 1px 1px rgba(46, 47, 66, .16), 0 2px 1px rgba(46, 47, 66, .08);
}

.search-form input {
  width: 800px;
  border: 1px solid #f0f0f0;
  border-radius: 2px;
  padding: 10px;
  font-size: 17px;
}

.search-form input:focus {
  outline: 2px solid #ffea00;
}

.search-form button {
  width: 100px;
  color: #000;
  cursor: pointer;
  background-color: #f0f0f0;
  border: 0 solid #f0f0f0;
  padding: 12.5px;
  position: absolute;
  transform: translate(-100%);
}

.search-form button:hover:focus {
  background-color: #ffea00;
}

.gallery {
  width: 1200px;
  height: 100%;
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 20px;
  margin: 100px auto 0;
  display: flex;
}

.photo-card {
  width: calc(25% - 20px);
  border: 1px solid #e7e9fc;
  border-top: none;
  box-shadow: 0 1px 6px rgba(46, 47, 66, .08);
}

.info {
  text-align: center;
  gap: 5px;
  padding: 15px 10px;
  display: flex;
}

.info-item {
  display: block;
}

.text {
  text-align: center;
  margin-top: 10px;
  display: none;
}

.loader {
  width: 1em;
  height: 1em;
  text-indent: -9999em;
  border-radius: 50%;
  font-size: 10px;
  animation: mulShdSpin 1.1s infinite;
  position: relative;
  transform: translateZ(0);
}

